@import './../others/root.css';

#header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--color-grey);
  box-shadow: 1px 1px 4px var(--color-dark3);
  padding: 0% 15%;
  margin-bottom: 5em;
}

.current_route {
  color: var(--color-dark4);
  font-weight: 500;
}

.language_btn {
  background-size: 100%;
  min-width: 2em;
  min-height: 2em;
  border-radius: 50%;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.language_label {
  color: black;
}

.language_btn:hover {
  opacity: 0.8;
}

.language_div {
  display: flex;
  align-items: center;
}

.navigation_header {
  display: flex;
  gap: 3%;
}

/* Media query para telas menores */
@media screen and (max-width: 666px) {
  .navigation_header {
    flex-direction: column; /* Altera a direção para coluna em telas menores */
    align-items: flex-start; /* Ajusta o alinhamento */
  }

  .navigation_header {
    flex-direction: column; /* Altera a direção para coluna em telas menores */
  }
}
